___CSS_LOADER_EXPORT___.push([module.id, `/* -------------------------
   Base (Mobile-First)
--------------------------- */
.about-section {
  box-sizing: border-box; /* helps prevent overflow */
  width: 100%;
  display: flex;
  flex-direction: column; /* stacked on mobile */
  align-items: center;
  justify-content: center;
  padding: 55px 20px 20px 20px;
  background-color: #f0f0f0;
  margin: 0 auto; /* centers the container */
}

/* Image container */
.about-image-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px; /* space below image on mobile */
}

/* Make sure the image is fully responsive */
.about-image {
  width: 100%;
  max-width: 500px;
  height: auto;
  border-radius: 10px;
  box-shadow: 
    10px 0 15px rgba(0, 0, 0, 0.1),
    -10px 0 15px rgba(0, 0, 0, 0.1);
}

/* Text container */
.about-content {
  width: 100%;
  max-width: 800px; /* prevents text from stretching too wide */
  margin: 0 auto;
}

.about-us-content-title {
  margin: 0;
  position: relative;
  text-align: center;
  color: rgb(35, 53, 51);
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.about-us-name {
  font-size: 1rem;
  font-weight: bolder;
  letter-spacing: 1.5px;
  margin: 20px 5px;
  z-index: 3;
}

.about-us-line {
  width: 30px;
  height: 3px;
  background-color: rgba(87, 128, 124, 0.75);
  margin: 20px 5px;
}

.about-us-company-name {
  font-size: 38px;
  font-weight: bolder;
  color: rgb(35, 53, 51);
  line-height: 40px;
  margin: 5px 2px 15px 2px;
  padding: 5px 0px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.about-us-paragraph {
  font-size: 1.1rem;
  font-family: sans-serif;
  color: black;
  margin: 5px 2px;
  padding: 5px 0px;
  letter-spacing: 1px;
  line-height: 30px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  font-weight: lighter;
}

.about-us-button {
  display: inline-block;
  padding: 12px 24px;
  margin-top: 25px;
  font-size: 1rem;
  font-family: Arial, sans-serif;
  font-weight: 600;
  color: rgb(35, 53, 51);
  background-color: transparent;
  border: 2px solid rgb(35, 53, 51);
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease, color 0.3s ease;
  line-height: 25px;
}

.about-us-button:hover {
  background-color: #354D4B;
  color: #fff;
}

/* -------------------------
   Desktop (769px and up)
--------------------------- */
@media (min-width: 769px) {
  .about-section {
    flex-direction: row; /* side-by-side on desktop */
    align-items: flex-start;
    justify-content: center;
    padding: 50px;
    max-width: 1200px; /* optional max container width */
  }

  .about-image-container,
  .about-content {
    width: 50%; /* half the row each */
    max-width: none;
    margin-bottom: 0;
  }

  /* Spacing between image and text */
  .about-image-container {
    margin-right: 30px;
  }
}
